@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@300;400;500;600;700&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
/*********************************/
/*             General           */
/*===============================*/
html {
  @apply relative scroll-smooth;
}

body {
  @apply font-manrope text-[15px] text-black;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  @apply font-manrope leading-normal font-semibold;
}

p {
  @apply leading-relaxed;
  @apply font-manrope;
}

.border-no {
  border: none !important;
}

::selection {
  @apply bg-primary text-white;
}

#toggle-count span [type=radio]:checked + label {
  @apply text-white bg-primary;
}

/********************/
/*     Buttons      */
/*==================*/
.btn {
  @apply py-2 px-8 inline-block font-medium tracking-wide border align-middle transition-all duration-500 ease-in-out text-base text-center;
  /*  Button icons */
  /* Button Link */
}
.btn.btn-lg {
  @apply py-2.5 px-6 text-lg;
}
.btn.btn-sm {
  @apply py-[5px] px-4 text-sm;
}
.btn.btn-icon {
  @apply p-0 h-9 w-9 inline-flex items-center text-center justify-center text-base;
}
.btn.btn-icon.btn-lg {
  @apply h-12 w-12;
}
.btn.btn-icon.btn-sm {
  @apply h-8 w-8;
}
.btn.btn-link {
  @apply relative p-0 border-none after:content-[""] after:absolute after:h-px after:w-0 after:right-0 after:bottom-0 after:left-0 after:transition-all after:duration-500;
}
.btn.btn-link:hover {
  @apply after:w-full after:right-auto;
}

/*********************************/
/*             Helper            */
/*===============================*/
.container,
.contacinr-fluid {
  @apply relative;
}

.text-type-element {
  @apply z-2 after:content-[""] after:absolute after:h-1 after:w-auto after:bg-orange-300 after:right-0 after:bottom-0 after:left-0 after:z-0 after:rounded-md after:transition-all after:duration-300 after:ease-in-out;
}

/*****************/
/*  back-to-top  */
/*****************/
.back-to-top {
  @apply hidden;
}